.product-details {
    margin-top: 100px !important;
  }
  
  .card-img-top{
    height: 380px !important;
  }
  .product-details-info {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
  }
  
  .product-details-info h6 {
    margin-bottom: 20px !important;
  }
  
  .similar-products {
    margin-top: 50px;
    width: 100%;
    /* overflow-x: auto; */
    /* white-space: nowrap; */
    /* overflow-x: hidden */
    display: flex;
    flex-direction: column;
  }
  

  .similar-products .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 18rem;
    margin-right: 10px;
  display: inline-block;
  }
  
  .similar-products .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .similar-products .card-price {
    color: green;
    font-weight: bold;
  }
  .similar-products button {
    border-radius: 0;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
  }
  
  .similar-products .card-text {
    color: rgb(90, 89, 89);
  }
  
  .similar-products .card img:hover {
    transform: scale(0.9);
  }
  
  .similar-products .card-img-top {
    height: 360px !important;
  }
  /* more details */
  .more {
    font-size: 1.2rem;
    /* padding: 0.1rem 0.1rem; */
    padding: 10px 20px;
    border: none;
    outline: none;
    border-radius: 0.4rem;
    cursor: pointer;
    text-transform: uppercase;
    background-color: rgb(14, 14, 26);
    color: rgb(234, 234, 234);
    font-weight: 700;
    transition: 0.6s;
    box-shadow: 0px 0px 3px #1f4c65;
    /* -webkit-box-reflect: below 10px linear-gradient(to bottom, rgba(0,0,0,0.0), rgba(0,0,0,0.4)); */
  }
  
  .more:active {
    scale: 0.92;
  }
  
  .more:hover {
    background: rgb(2,29,78);
    background: linear-gradient(270deg, rgba(2, 29, 78, 0.681) 0%, rgba(31, 215, 232, 0.873) 60%);
    color: rgb(4, 4, 38);
  }