.cart-page {
  margin-top: 64px;
  
}

.cart-page h1 {
  padding: 15px !important;
  font-family: "roboto", sans-serif;
  font-weight: normal;
  background-color: rgba(0, 0, 255, 0.072) !important;
}
.cart-page h1 > p {
  font-size: 20px;
  margin-top: 10px;
}

.cart-page .card {
  padding: 5px !important;
  height: 390px !important;
  margin-bottom: 5px !important;
}
.cart-page .cart-remove-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}
.cart-page .cart-summary {
  text-align: center;
  /* margin-top: -140px !important; */
}
/* responsive */
.cart-page {
  margin: 50px 0;
}

@media (max-width: 767px) {
  .row {
    flex-wrap: wrap;
  }

  .col-md-7 {
    flex-basis: 100%;
  }

  .col-md-4 {
    flex-basis: 50%;
  }

  .cart-remove-btn {
    margin-top: 10px;
  }
  .cart-summary {
    margin-top: 20px; /* Add some margin to the top of the cart summary */
  }
    .cart-summary {
      margin-bottom: 20px !important; /* Add some margin to the top of the cart summary */
      padding: 30px;
    }
  }

@media (min-width: 768px) {
  .row {
    flex-wrap: nowrap;
  }

  .col-md-7 {
    flex-basis: 70%;
  }

  .col-md-4 {
    flex-basis: 30%;
  }
  .cart-summary {
    margin-top: 20px; /* Add some margin to the top of the cart summary */
    padding: 30px;
  }
}

